
import { defineComponent } from "vue";
import { useFetch } from "../../use/useFetch";
import { dateFormat } from "../../format";

export default defineComponent({
  setup() {
    const { loading, data, fetch } = useFetch({
      url: "h5/validate/list",
      paginated: true,
    });

    fetch();

    return {
      items: data,
      dateFormat,
    };
  },
});
