
import { defineComponent, PropType } from "vue";
import { useRouter } from "vue-router";

export default defineComponent({
  props: {
    theme: {
      type: String as PropType<"light" | "blue">,
      default: "blue",
    },
    title: {
      type: String,
      default: "天和保",
    },
    showBack: {
      type: Boolean,
      default: true,
    },
    onBackClick: {
      type: Function,
    },
  },
  setup(props) {
    const router = useRouter();

    const onClick = () => {
      if (typeof props.onBackClick === "function") {
        props.onBackClick();
        return;
      }
      router.back();
    };

    return {
      onClick,
    };
  },
});
